<template>
    <div :class="[`${classPrefix}`]">
        <tree-node v-show="!filterLoading" :data="currentData"></tree-node>
        <div :class="[`${classPrefix}__empty`]" v-if="isEmpty">{{localEmptyText}}</div>
    </div>
</template>

<script>
    import Config from 'main/config/config'
    import TreeNode from './tree-node'
    import Locale from 'main/mixins/locale'
    import Mixins from './mixins'

    export default {
        name: "Tree",
        mixins: [Locale, Mixins],
        provide() {
            return {
                treeRoot: this
            }
        },
        props: {
            data: {
                type: Array,
                default() {
                    return []
                }
            },
            //px
            indent: {
                type: [String, Number],
                default: 16
            },
            //node 
            filterNodeMethods: {
                type: Function,
                default() {
                    return function () {
                        return true
                    }
                }
            },
            //
            defaultExpandAll: Boolean,
            //()
            hideIcon: Boolean,
            //
            expandIconClass: {
                type: String,
                default: 'right'
            },
            //
            loadingIconClass: {
                type: String,
                default: 'loading'
            },
            //Promise
            loadData: {
                type: Function
            },
            accordion: Boolean,//
            emptyText: String,
            showCheckbox: Boolean,
            checkStrictly: Boolean,//checkbox
            expandKeys: {
                type: Array,
                default() {
                    return [];
                }
            },
            checkedKeys: {
                type: Array,
                default() {
                    return [];
                }
            },
            renderContent: {
                type: Function
            },
            expandClickOnNode: {
                type: Boolean,
                default: true
            },//
        },
        data() {
            return {
                classPrefix: Config.classPrefix + '-tree',
                componentName: 'Tree',
                renderType: 'normal',
                filterLoading: false,//
            }
        },
        computed: {
            localEmptyText() {
                return this.emptyText ? this.emptyText : this.t('m.tree.emptyData');
            },
            isEmpty() {
                if (this.currentData && this.currentData.length) {
                    let visibleData = this.currentData.filter(item => {
                        return !!item.__visible
                    });
                    return !visibleData.length;
                }
                return !this.currentData || !this.currentData.length;
            }
        },
        components: {
            TreeNode
        },
        mounted() {
            this.$nextTick(() => {
                this.setRenderType();
            })
        },
        methods: {
            setRenderType() {
                this.renderType = 'normal';
                if (this.$scopedSlots.default) {
                    this.renderType = 'slot';
                }
                if (this.renderContent) {
                    this.renderType = 'render';
                }
            },

            getExpandNodes() {
                return this.getDataByPropValue('__expand', true).data;
            },
            getCheckedNodes() {
                return this.getDataByPropValue('__checked', true).data;
            },
            getSelectedNodes() {
                return this.getDataByPropValue('__selected', true).data;
            },

            filterNodes(value) {
                this.filterLoading = true;

                if (!value.trim()) {
                    this.reduceData.forEach(item => {
                        item.__visible = true;
                        item.__expand = true;
                    });
                } else {
                    this.reduceData.forEach(item => {
                        item.__visible = false;
                        item.__expand = false;
                    });
                    let data = this.getDataByfilterValue(value);
                    data.forEach(item => {
                        this.setReduceDataProp(item, '__visible', true);
                        this.setReduceDataProp(item, '__expand', true);
                    });
                }

                this.$nextTick(() => {
                    this.filterLoading = false;
                });
            },

            //
            getData() {
                return JSON.parse(JSON.stringify(this.reduceData));
            },

            expandChange(item) {
                let expandData = this.getExpandNodes();
                this.$emit('expand-change', expandData, item);
            },
            checkChange(item) {
                let checkedData = this.getCheckedNodes();
                this.$emit('check-change', checkedData, item);
            }
        },
    }
</script>
