<template>
    <span :class="[`${classPrefix}`]">
        {{currentTime}}
    </span>
</template>

<script>
    import Config from 'main/config/config'
    import {dateFormat} from "main/utils/date";

    export default {
        name: "Time",
        props: {
            time: {
                type: [String, Number, Date],
                required: true,
                default: ''
            },
            type: {
                type: String,
                default: 'relative',
                validator(value) {
                    return ['relative', 'date', 'datetime'].includes(value)
                }
            },//relative..1..,(format) (format)(format)
            format: {
                type: String,
                default: ''
            },
            //
            interval: {
                type: Number,
            }
        },
        data() {
            return {
                classPrefix: Config.classPrefix + '-time',
                defaultFormat: 'yyyy-MM-dd hh:mm:ss',
                currentFormatDate: '',//
                currentFormatTime: '',//
                currentTime: '',
                timer: null,
            }
        },
        mounted() {
            this.initFormat();
        },
        methods: {
            initFormat() {
                let format = this.format || this.defaultFormat;
                if (this.type === 'date') {
                    format = this.format || 'yyyy-MM-dd'
                }
                let splitIndex = format.indexOf(' ');
                this.currentFormatDate = format.substring(0, splitIndex).trim() || 'yyyy-MM-dd';
                this.currentFormatTime = format.substring(splitIndex + 1).trim() || 'hh:mm:ss';

                this.initCurrentTime();
                if(this.interval !== undefined && this.interval > 0){
                    let interval = parseInt(this.interval) * 1000;
                    clearInterval(this.timer);
                    this.timer = setInterval(() => {
                        this.initCurrentTime();
                    }, interval)
                }
            },
            initCurrentTime() {
                let time = '';
                switch (this.type) {
                    case 'relative':
                        time = this.autoTime(this.time);
                        break;
                    case 'date':
                        time = dateFormat(this.time, this.currentFormatDate)
                        break;
                    case 'datetime':
                        time = dateFormat(this.time, this.format || this.defaultFormat)
                        break;
                }
                this.currentTime = time;
            },
            autoTime(time) {
                if (!time) return;
                let result = '';
                let nowDate = new Date();
                let yesterDate = new Date();
                let tomorrowDate = new Date();
                yesterDate.setDate(nowDate.getDate() - 1);
                tomorrowDate.setDate(nowDate.getDate() + 1);
                let date = new Date(time);

                let disTimeStamp = nowDate - date;

                let oneHoursTimeStamp = 60 * 60 * 1000;//
                let oneMinutesTimeStamp = 60 * 1000;//

                let nowDay = nowDate.getDate();//
                let yesterDay = yesterDate.getDate();//
                let tomorrowDay = tomorrowDate.getDate();//
                let day = date.getDate();//

                if (disTimeStamp >= 0) {
                    //
                    if (disTimeStamp <= oneMinutesTimeStamp) {
                        result = parseInt(disTimeStamp / 1000) + '';
                    }
                    //
                    else if (disTimeStamp <= oneHoursTimeStamp) {
                        result = parseInt(disTimeStamp / 1000 / 60) + '';
                    }
                    //
                    else if (nowDay === day) {
                        result = dateFormat(date, this.currentFormatTime);
                    }
                    //
                    else if (yesterDay === day) {
                        result = '' + dateFormat(date, this.currentFormatTime);
                    } else {
                        result = dateFormat(date, this.format || this.defaultFormat);
                    }
                } else {
                    //

                    disTimeStamp = Math.abs(disTimeStamp);
                    //
                    if (disTimeStamp <= oneMinutesTimeStamp) {
                        result = parseInt(disTimeStamp / 1000) + '';
                    }
                    //
                    else if (disTimeStamp <= oneHoursTimeStamp) {
                        result = parseInt(disTimeStamp / 1000 / 60) + '';
                    }
                    //
                    else if (nowDay === day) {
                        result = parseInt(disTimeStamp / 1000 / 60 / 60) + '';
                    }
                    //
                    else if (tomorrowDay === day) {
                        result = '' + dateFormat(date, this.currentFormatTime);
                    } else {
                        result = dateFormat(date, this.format || this.defaultFormat);
                    }
                }
                return result;
            },
        },
        destroyed() {
            this.timer && clearInterval(this.timer);
        },
        watch: {
            value() {
                this.initFormat();
            }
        }
    }
</script>