<template>
    <vuescroll :class="[`${classPrefix}`]" :ops="options" ref="vuescroll">
        <slot></slot>
    </vuescroll>
</template>

<script>
    import Config from 'main/config/config'
    import vuescroll from 'vuescroll';

    export default {
        name: "Scroll",
        props: {
            size: String,
            scrollOption: {
                type: Object,
                default: function () {
                    return {}
                }
            },
        },
        data() {
            return {
                classPrefix: Config.classPrefix + '-scroll',
                ops: {
                    vuescroll: {
                        mode: 'native',
                        sizeStrategy: 'percent',
                        detectResize: true,
                    },
                    scrollPanel: {
                        initialScrollY: false,//mounted
                        initialScrollX: false,
                        scrollingX: true,// x  y 
                        scrollingY: true,
                        speed: 300,
                        easing: undefined,
                        verticalNativeBarPos: 'right'
                    },
                    rail: {
                        background: '#cecece',//
                        opacity: 0,//
                        size: '6px',//
                        specifyBorderRadius: false,// borderRadius 
                        gutterOfEnds: null,// x  y 
                        gutterOfSide: '1px',//
                        keepShow: false,// bar 
                    },
                    bar: {
                        showDelay: 500,//
                        onlyShowBarOnScroll: true,// bar
                        keepShow: false,//
                        background: '#c1c1c1',//
                        opacity: 1,//
                        hoverStyle: false,
                        specifyBorderRadius: false,// borderRadius 
                        minSize: false,
                        size: '6px',
                        disable: false,//
                    }
                }
            }
        },
        computed: {
            options() {
                let ops = JSON.parse(JSON.stringify(this.ops));
                if (this.size === 'small') {
                    ops.bar.size = '4px'
                }
                Object.keys(this.scrollOption).forEach(key => {
                    Object.keys(this.scrollOption[key]).forEach(_key => {
                        ops[key][_key] = this.scrollOption[key][_key]
                    })
                });
                return Object.assign({}, ops);
            }
        },
        components: {
            vuescroll
        },
        methods: {
            scrollTo(position, speed = 500) {
                this.$refs.vuescroll.scrollTo(position, speed)
            }
        }
    }
</script>
